import { useState, useEffect } from "react";
import styled from "@emotion/styled";
import { v4 as uuid } from "uuid";
import { __ } from "@wordpress/i18n";
import { flatten, differenceBy } from "lodash";
import Box from "@mui/material/Box";
import AddIcon from "@mui/icons-material/Add";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import IconButton from "@mui/material/IconButton";
import Avatar from "@mui/material/Avatar";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionActions from "@mui/material/AccordionActions";

import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardMedia from "@mui/material/CardMedia";
import CardContent from "@mui/material/CardContent";

import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import Grid from "@mui/material/Grid";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import Collapse from "@mui/material/Collapse";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";

import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import ListSubheader from "@mui/material/ListSubheader";
import { List, ListItem, ListItemText, Tooltip } from "@mui/material";
import Select from "@mui/material/Select";
import Chip from "@mui/material/Chip";
import Button from "@mui/material/Button";
import LoadingButton from "@mui/lab/LoadingButton";
import SaveIcon from "@mui/icons-material/Save";
import Checkbox from "@mui/material/Checkbox";
import Snackbar from "@mui/material/Snackbar";

import {
  ComponentMedia,
  FormControlFull,
  FormControlImage,
  FormControllSettings,
  AccordionHeaderInner,
  CardMediaEdit,
  IconButtonAdd,
  Alert,
} from "./PizzaComponents";

const ShortCodeWrapper = styled.div`
  display: flex;
  align-items: flex-start;
`;

const ShortCodeNav = styled.div`
  width: 20%;
  border-right: 1px solid #ddd;
  padding-right: 20px;
`;
const ShortCodeContent = styled.div`
  width: 80%;
  margin-left: 80px;
`;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: 48 * 4.5 + 8,
      width: 250,
    },
  },
};
const PizzaShortCode = ({ data: pizzaComponents }) => {
  const [shortcodes, setShortcodes] = useState(EV_PIZZA_DATA.pizza_shortcodes || []);
  const [activeShortcode, setActiveShortcode] = useState(shortcodes.length > 0 ? shortcodes[0] : null);
  const shortCodesIds = shortcodes.map((s) => s.id);
  const getRandomId = () => {
    const multiplyValue = shortcodes.length > 99 ? 1000 : 100;
    const randomValue = Math.floor(Math.random() * multiplyValue);
    if (shortCodesIds.includes(randomValue)) {
      return getRandomId();
    }
    return randomValue;
  };

  const emptyShortcode = {
    id: getRandomId(),
    title: "New shortcode",
    type: "modal",
    steps: 1,
    components: [
      {
        step: 1,
        staticTitle: "Step 1",
        title: "",
        required: true,
        components: [],
      },
    ],
  };
  const [expanded, setExpanded] = useState({});

  const [loading, setLoading] = useState(false);

  const [message, setMessage] = useState({
    opened: false,
    text: "",
    type: "",
  });

  const handleChangeAcc = (id) => (event, newExpanded) => {
    // console.log(id, event, newExpanded);
    setExpanded(newExpanded ? { ...expanded, [id]: true } : { ...expanded, [id]: false });
  };

  const [expandEdit, setExpandEdit] = useState({});
  const handleExpandEdit = (id) => {
    setExpandEdit({ ...expandEdit, [id]: !expandEdit[id] });
  };
  const addShortcode = () => {
    let clearList = false;

    if (shortcodes.length === 0) {
      clearList = true;
    }
    const newShortCodeEmpty = { ...emptyShortcode };
    setShortcodes([...shortcodes, newShortCodeEmpty]);

    if (clearList) {
      setActiveShortcode(newShortCodeEmpty);
    }
  };


  const changeTitle = (e) => {
    setActiveShortcode({ ...activeShortcode, title: e.target.value });
  };


  const changeSteps = (e) => {
    const indexTarget = parseInt(e.target.value);
    if (indexTarget < 0) {
      return;
    }
    // console.log(indexTarget);
    let newComponents = [];
    if (activeShortcode.components.length > 0) {
      if (activeShortcode.components.length > indexTarget) {
        newComponents = activeShortcode.components.filter((c, index) => index + 1 <= indexTarget);
      } else {
        newComponents = [
          ...activeShortcode.components,
          ...Array.from({ length: indexTarget - activeShortcode.components.length }, (v, k) => k).map((el) => ({
            step: el + 1 + activeShortcode.components.length,
            title: "",
            staticTitle: `Step ${el + 1 + activeShortcode.components.length}`,
            components: [],
            required: true,
          })),
        ];
      }
    } else {
      newComponents = Array.from({ length: indexTarget }, (v, k) => k).map((el) => ({
        step: el + 1,
        title: "",
        staticTitle: `Step ${el + 1}`,
        components: [],
        required: true,
      }));
    }
    // console.log(newComponents);
    setActiveShortcode({
      ...activeShortcode,
      steps: e.target.value,
      components: isNaN(indexTarget) ? activeShortcode.components : newComponents,
    });
  };

  const handleChangeType = (e) => {
	setActiveShortcode({
		...activeShortcode,
		type: e.target.value,
	  });
  }


  const selectShortcode = (shortcode) => {
    setActiveShortcode(shortcode);
  };

  useEffect(() => {
    if (!activeShortcode) {
      return;
    }
    const newShortcodes = shortcodes.map((s) => (s.id === activeShortcode.id ? activeShortcode : s));
    setShortcodes(newShortcodes);
  }, [activeShortcode]);

  const getShortCodeValue = () => {
	const shortCodeType = activeShortcode.type === 'steps-slider' || activeShortcode.type ==='modal' ? "modal" : 'inline';
    return `[pbw-builder id=${activeShortcode.id} steps=${activeShortcode.steps} title="${activeShortcode.title}" type="${shortCodeType}"]`;
  };

  const handleChangeComponents = (e, stepId) => {
    if (e.target.value === "") return;
    const selectedValues = typeof e.target.value === "string" ? e.target.value.split(",") : e.target.value;
    const selectedComponentsModified = selectedValues.map((id) => {
      const foundComponent = flatten(pizzaComponents.map((group) => group.components)).find((component) => {
        return component.id === id;
      });
      //   console.log(foundComponent);
      return { ...foundComponent };
    });
    const newSteps = activeShortcode.components.map((c) => {
      return c.step === stepId ? { ...c, components: selectedComponentsModified } : c;
    });
    // const selectedComponentsModified = selectedValues.map((id) => {
    // 	const foundComponentAlready = productPizzaFull?.consists_of?.consists.find(
    // 	  (component) => {
    // 		return component.id === id;
    // 	  }
    // 	);
    // 	if (foundComponentAlready) {
    // 	  return foundComponentAlready;
    // 	} else {
    // 	  const foundComponent = flatten(
    // 		pizzaComponents.map((group) => group.components)
    // 	  ).find((component) => {
    // 		return component.id === id;
    // 	  });
    // 	  return {
    // 		...foundComponent,
    // 		position: "",
    // 		required: false,
    // 		visible: true,
    // 	  };
    // 	}
    //   });
    // console.log(newSteps);
    setActiveShortcode({ ...activeShortcode, components: newSteps });
  };
  const setStepTitle = (e, stepId) => {
    const newComponents = activeShortcode.components.map((c) => {
      return c.step === stepId ? { ...c, title: e.target.value } : c;
    });
    setActiveShortcode({ ...activeShortcode, components: newComponents });
  };
  const setStepRequired = (e, stepId) => {
    const newComponents = activeShortcode.components.map((c) => {
      return c.step === stepId ? { ...c, required: e.target.checked } : c;
    });
    setActiveShortcode({ ...activeShortcode, components: newComponents });
  };
  const deleteStep = (stepId) => {
    const newComponents = activeShortcode.components
      .filter((c) => c.step !== stepId)
      .map((c, index) => ({
        ...c,
        step: index + 1,
        staticTitle: `Step ${index + 1}`,
      }));
    // console.log(newComponents);
    setActiveShortcode({
      ...activeShortcode,
      steps: newComponents.length,
      components: newComponents,
    });
  };

  //edit components
  const setComponentName = (e, componentId, stepId) => {
    const newComponents = activeShortcode.components.map((c) => {
      const newCompos = c.components.map((compos) => (compos.id === componentId ? { ...compos, name: e.target.value } : compos));
      return c.step === stepId ? { ...c, components: newCompos } : c;
    });
    setActiveShortcode({ ...activeShortcode, components: newComponents });
  };
  const setComponentPrice = (e, componentId, stepId) => {
    const newComponents = activeShortcode.components.map((c) => {
      const newCompos = c.components.map((compos) => (compos.id === componentId ? { ...compos, price: e.target.value } : compos));
      return c.step === stepId ? { ...c, components: newCompos } : c;
    });
    setActiveShortcode({ ...activeShortcode, components: newComponents });
  };
  const setComponentWeight = (e, componentId, stepId) => {
    const newComponents = activeShortcode.components.map((c) => {
      const newCompos = c.components.map((compos) => (compos.id === componentId ? { ...compos, weight: e.target.value } : compos));
      return c.step === stepId ? { ...c, components: newCompos } : c;
    });
    setActiveShortcode({ ...activeShortcode, components: newComponents });
  };
  const setComponentDescription = (e, componentId, stepId) => {
    const newComponents = activeShortcode.components.map((c) => {
      const newCompos = c.components.map((compos) => (compos.id === componentId ? { ...compos, description: e.target.value } : compos));
      return c.step === stepId ? { ...c, components: newCompos } : c;
    });
    setActiveShortcode({ ...activeShortcode, components: newComponents });
  };



  const saveData = () => {
    setLoading(true);
    const formData = new FormData();
    const abortController = new AbortController();
    formData.append("action", "ev_pizza_save_shortcodes");
    formData.append("data", JSON.stringify(shortcodes));
    formData.append("nonce", EV_PIZZA_DATA.nonce);
    try {
      fetch(EV_PIZZA_DATA.ajax_url, {
        method: "POST",
        body: formData,
        signal: abortController.signal,
      })
        .then((response) => {
          return response.json();
        })
        .then((res) => {
          //console.log( res );
          if (res.success) {
            setMessage({
              opened: true,
              type: "success",
              text: __("Shortcode was saved!", "pizza-builder-for-woocommerce"),
            });
          } else {
            setMessage({
              opened: true,
              type: "error",
              text: __("Something went wrong", "pizza-builder-for-woocommerce"),
            });
          }
          setLoading(false);
        });
    } catch (err) {
      if (!abortController.signal.aborted) {
        setLoading(false);
        setMessage({
          opened: true,
          type: "error",
          text: __("Something went wrong", "pizza-builder-for-woocommerce"),
        });
      }
      console.log(err);
    }
  };

  const renderFooter = () => {
	return (
	  <Box sx={{marginTop: '50px'}}>
		<LoadingButton
		  color="success"
		  onClick={saveData}
		  loading={loading}
		  loadingPosition="start"
		  startIcon={<SaveIcon />}
		  variant="contained"
		>
		  {__("Save", "pizza-builder-for-woocommerce")}
		</LoadingButton>
		<Snackbar
		  anchorOrigin={{ vertical: "top", horizontal: "right" }}
		  open={message.opened}
		  autoHideDuration={4000}
		  onClose={() => setMessage({ ...message, opened: false })}
		  key={"top-right"}
		  sx={{ top: "70px !important" }}
		>
		  <div>
			<Alert type={message.type}>{message.text}</Alert>
		  </div>
		</Snackbar>
	  </Box>
	)
  }

  const renderContent = (shortcode) => {

	
	if(shortcode) {
		return (
			<ShortCodeContent>
            <div
              style={{
                padding: "15px",
                backgroundColor: "cornsilk",
                color: "cornflowerblue",
              }}
            >
              <Typography>
                {__(`Your shortcode: `)}
                <span style={{ fontWeight: "700" }}>{getShortCodeValue()}</span>
              </Typography>
            </div>
            <FormControl sx={{ mt: 3, flexDirection: "row", mb: 3, flexWrap: 'wrap' }}>
              <TextField type="text" label="Title" value={activeShortcode.title} onChange={changeTitle} sx={{ mr: 3 }} />

              <TextField type="number" label={__('Steps')} sx={{ mr: 3 }} helperText="Only whole numbers" value={activeShortcode.steps} onChange={changeSteps} />
			  <FormControl sx={{minWidth:'200px'}}>
				<InputLabel >{__("Type")}</InputLabel>
				<Select value={activeShortcode?.type === 'steps-slider' ? "modal" : activeShortcode.type} label={__("Type")} onChange={handleChangeType}>
					
					<MenuItem value="modal">{__('Modal')}</MenuItem>
					<MenuItem value="inline">{__('Inline')}</MenuItem>
					
				</Select>
			</FormControl>
            </FormControl>
            {activeShortcode.components.length > 0 && (
              <Typography component="h4" variant="h4" sx={{ padding: "10px 0" }}>
                {__("Steps:", "pizza-builder-for-woocommerce")}
              </Typography>
            )}
            {activeShortcode.components.map((c) => (
              <Accordion
                key={`comp-${c.step}`}
                expanded={expanded.hasOwnProperty(c.step) ? expanded[c.step] : false}
                onChange={handleChangeAcc(c.step)}
              >
                <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1a-content" id={`panel1a-header-${c.step}`}>
                  <Typography>{c.staticTitle + (c.title !== "" ? " - " + c.title : "")}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <AccordionHeaderInner>
                    <FormControl sx={{ mt: 2 }}>
                      <TextField type="text" label={__("Step name")} value={c.title} onChange={(e) => setStepTitle(e, c.step)} />
                    </FormControl>
                  </AccordionHeaderInner>

                  <FormControl sx={{ mt: 3, width: 400 }}>
                    <InputLabel id={`food-add-chip-label-${c.step}`}>{__("Add components", "pizza-builder-for-woocommerce")}</InputLabel>
                    <Select
                      labelId={`food-add-chip-label-${c.step}`}
                      multiple
                      value={c.components.map((component) => component.id)}
                      onChange={(e) => handleChangeComponents(e, c.step)}
                      input={<OutlinedInput label={__("Add components", "pizza-builder-for-woocommerce")} />}
                      renderValue={(selected) => (
                        <Box sx={{ display: "flex", flexWrap: "wrap", gap: 0.5 }}>
                          {selected.map((value) => {
                            const foundLabel = flatten(pizzaComponents.map((group) => group.components)).find((component) => {
                              return component.id === value;
                            });

                            return <Chip key={value} label={foundLabel.name} />;
                          })}
                        </Box>
                      )}
                      MenuProps={MenuProps}
                    >
                      {pizzaComponents.map((group) => {
                        return [
                          <ListSubheader color="primary" sx={{ fontSize: "20px", fontWeight: "bold" }} key={group.id}>
                            {group.groupName}
                          </ListSubheader>,
                          group.components.map((component) => {
                            return (
                              <MenuItem
                                key={component.id}
                                value={component.id}
                                //   style={getStyles(name, personName, theme)}
                              >
                                {component.name}
                              </MenuItem>
                            );
                          }),
                        ];
                      })}
                    </Select>
                  </FormControl>

                  {c.components.length > 0 && (
                    <Typography component="h4" variant="h4" sx={{ padding: "10px 0" }}>
                      {__("Components:", "pizza-builder-for-woocommerce")}
                    </Typography>
                  )}
                  <Grid container spacing={2}>
                    {c.components.map((component) => (
                      <Grid item xs={6} md={4} sm={4} xl={3} key={`comp-${component.id}`}>
                        <Card>
                          <CardHeader
                            avatar={
                              <Avatar aria-label="component">
                                {component.name.split("").length > 0 ? component.name.split("")[0].toUpperCase() : ""}
                              </Avatar>
                            }
                            action={
                              <div>
                                <IconButton aria-label="edit" color="secondary" onClick={() => handleExpandEdit(component.id)}>
                                  <EditIcon />
                                </IconButton>
                              </div>
                            }
                            title={component.name}
                            subheader={
                              <span>
                                {component.weight !== "" && `${component.weight}/`}
                                {`${component.price} ${EV_PIZZA_DATA.wc_symbol}`}
                              </span>
                            }
							sx={{flexWrap: 'wrap'}}
                          ></CardHeader>
                          <Collapse
                            in={expandEdit.hasOwnProperty(component.id) ? expandEdit[component.id] : false}
                            timeout="auto"
                            unmountOnExit
                          >
                            <FormControlFull>
                              <TextField
                                type="text"
                                label={__("Name")}
                                value={component.name}
                                onChange={(e) => setComponentName(e, component.id, c.step)}
                              />
                            </FormControlFull>
                            <FormControlFull>
                              <TextField
                                type="text"
                                label={__("Price")}
                                value={component.price}
                                onChange={(e) => setComponentPrice(e, component.id, c.step)}
                              />
                            </FormControlFull>

                            <FormControlFull>
                              <TextField
                                label={__("Weight")}
                                value={component.weight}
                                onChange={(e) => setComponentWeight(e, component.id, c.step)}
                              />
                            </FormControlFull>
                            <FormControlFull>
                              <TextField
                                label={__("Description")}
                                multiline
                                maxRows={4}
                                value={component.description}
                                onChange={(e) => setComponentDescription(e, component.id, c.step)}
                              />
                            </FormControlFull>
                          </Collapse>
                          {(expandEdit.hasOwnProperty(component.id) ? !expandEdit[component.id] : true) && (
                            <>
                              <ComponentMedia component="img" height="100" image={component.image} alt="" />
                              <CardContent>
                                <Typography variant="body2" color="text.secondary">
                                  {component.description}
                                </Typography>
                              </CardContent>
                            </>
                          )}
                        </Card>
                      </Grid>
                    ))}
                  </Grid>
                </AccordionDetails>
                <AccordionActions sx={{ justifyContent: "space-between" }}>
                  <FormControl>
                    <FormControlLabel
                      control={<Checkbox checked={c.required} onChange={(e) => setStepRequired(e, c.step)} color="primary" />}
                      sx={{ m: 0 }}
                      label={__("Required")}
                    />
                  </FormControl>
                  <Button variant="contained" color="error" onClick={() => deleteStep(c.step)}>
                    {__("Delete step")}
                  </Button>
                </AccordionActions>
              </Accordion>
            ))}
			{renderFooter()}
          </ShortCodeContent>
		)
	}

	else {
		return (
			<ShortCodeContent>
				<Typography component="h4" variant="h4">
					{__("Select shortcode")}
				</Typography>
				
			</ShortCodeContent>
	
		)
	}
  }

  return (
    <Box>
      <ShortCodeWrapper>
        <input type="hidden" name="ev_pizza_shortcodes" value={JSON.stringify(shortcodes)} />
        <ShortCodeNav>
          <List component="ul">
            {shortcodes.map((shortcode) => (
              <ListItem key={`short-${shortcode.id}`} onClick={() => selectShortcode(shortcode)} divider button>
                <ListItemText primary={shortcode.title} />
              </ListItem>
            ))}
          </List>

          <Tooltip title={__("Add shortcode")} placement="top">
            <IconButtonAdd color="inherit" size="large" onClick={addShortcode}>
              <AddIcon />
            </IconButtonAdd>
          </Tooltip>
        </ShortCodeNav>
 
          <ShortCodeContent>
			{renderContent(activeShortcode)}
          </ShortCodeContent>
    
      </ShortCodeWrapper>
    </Box>
  );
};

export default PizzaShortCode;
