import { useState, useEffect } from "react";
import { render, createRoot } from "@wordpress/element";
import { __ } from "@wordpress/i18n";
import PropTypes from "prop-types";
import AddIcon from "@mui/icons-material/Add";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import IconButton from "@mui/material/IconButton";
import Avatar from "@mui/material/Avatar";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Divider from "@mui/material/Divider";

import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Badge from "@mui/material/Badge";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionActions from "@mui/material/AccordionActions";

import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardMedia from "@mui/material/CardMedia";
import CardContent from "@mui/material/CardContent";

import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import Grid from "@mui/material/Grid";
import FormControl from "@mui/material/FormControl";
import Collapse from "@mui/material/Collapse";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";
import FormHelperText from "@mui/material/FormHelperText";

import Button from "@mui/material/Button";
import LoadingButton from "@mui/lab/LoadingButton";
import SaveIcon from "@mui/icons-material/Save";
import Snackbar from "@mui/material/Snackbar";

import InputLabel from "@mui/material/InputLabel";

import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import MenuItem from "@mui/material/MenuItem";

import Select from "@mui/material/Select";

import { v4 as uuid } from "uuid";

import Prism from "prismjs";
import PizzaShortCode from "./components/PizzaShortcode";
import {
  ComponentMedia,
  FormControlFull,
  FormControlImage,
  FormControllSettings,
  AccordionHeaderInner,
  CardMediaEdit,
  IconButtonAdd,
  Alert,
} from "./components/PizzaComponents";
import { Tooltip } from "@mui/material";
// import "prismjs/themes/prism-tomorrow.css";

function PizzaCode(props) {
  const { lang, code } = props;
  useEffect(() => {
    Prism.highlightAll();
  }, []);
  return (
    <div className="Code">
      <pre className="line-numbers">
        <code className={`language-${lang}`}>{code}</code>
      </pre>
    </div>
  );
}
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div role="tabpanel" hidden={value !== index} id={`pizza-tabpanel-${index}`} aria-labelledby={`pizza-tab-${index}`} {...other}>
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}
TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};
function tabProps(index) {
  return {
    id: `pizza-tab-${index}`,
    "aria-controls": `pizza-tabpanel-${index}`,
  };
}
const AdminPizza = () => {
  const [tab, setTab] = useState(0);
  const handleChangeTab = (event, newValue) => {
    setTab(newValue);
  };

  const [expanded, setExpanded] = useState({});

  const handleChangeAcc = (id) => (event, newExpanded) => {
    // console.log(id, event, newExpanded);
    setExpanded(newExpanded ? { ...expanded, [id]: true } : { ...expanded, [id]: false });
  };

  const [expandEdit, setExpandEdit] = useState({});
  const handleExpandEdit = (id) => {
    setExpandEdit({ ...expandEdit, [id]: !expandEdit[id] });
  };
  const [newComponent, setNewComponent] = useState({
    id: uuid(),
    name: "New",
    price: 0,
    weight: "",
    meta: false,
    image_ID: 0,
    image: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
    description: "",
  });
  const [pizzaSettings, setPizzaSettings] = useState(
    EV_PIZZA_DATA.pizza_settings || {
      empty_layer: {
        image: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
      },
      empty_side: {
        image: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
      },
      tipps: {
        enabled: false,
      },
      meta_popup: true,
	  smooth_attributes: [],
      redirect_cart: false,
      shortcode_disable: false,
      quick_view: true,
    },
  );
  const [pizzaComponents, setPizzaComponents] = useState(
    EV_PIZZA_DATA.pizza_components || [
      {
        id: uuid(),
        groupName: "group1",
        groupImage: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
        components: [
          {
            id: uuid(),
            name: "Chedar",
            price: 100,
            weight: "",
            meta: false,
            image_ID: 0,
            image: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
            description:
              "This impressive paella is a perfect party dish and a fun meal to cook together with your guests. Add 1 cup of frozen peas along with the mussels, if you like.",
          },
          {
            id: uuid(),
            name: "Gauda",
            price: 120,
            weight: "",
            meta: false,
            image_ID: 0,
            image: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
            description:
              "This impressive paella is a perfect party dish and a fun meal to cook together with your guests. Add 1 cup of frozen peas along with the mussels, if you like.",
          },
        ],
      },
      {
        id: uuid(),
        groupName: "group2",
        groupImage: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
        components: [
          {
            id: uuid(),
            name: "Becon",
            price: 80,
            meta: false,
            weight: "",
            image_ID: 0,
            image: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
            description:
              "This impressive paella is a perfect party dish and a fun meal to cook together with your guests. Add 1 cup of frozen peas along with the mussels, if you like.",
          },
          {
            id: uuid(),
            name: "Sousige",
            price: 85,
            image_ID: 0,
            weight: "",
            meta: false,
            image: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
            description:
              "This impressive paella is a perfect party dish and a fun meal to cook together with your guests. Add 1 cup of frozen peas along with the mussels, if you like.",
          },
        ],
      },
    ],
  );

  const [loading, setLoading] = useState(false);

  const [message, setMessage] = useState({
    opened: false,
    text: "",
    type: "",
  });

  let frame;
  const uploadImage = (id) => {
    // console.log(id);
    if (frame) {
      frame.open();
      return;
    }
    frame = wp.media({
      multiple: "false",
    });
    frame.on("select", function () {
      let attachment = frame.state().get("selection").first().toJSON();
      //   console.log(attachment);
      const newPizza = pizzaComponents.map((group) => {

		const newGroup = group.components.map((component) =>
		component.id === id
		  ? {
			  ...component,
			  image: attachment.sizes.hasOwnProperty("thumbnail") ? attachment.sizes.thumbnail.url : attachment.sizes.full.url,
			  image_ID: attachment.id,
			}
		  : component
	  );
		
		if (typeof group.groups !== "undefined") {
			const subgorups = group.groups.map((subg) => {
				const newSubComponent = subg.components.map((component) =>
				component.id === id ? {
					...component,
					image: attachment.sizes.hasOwnProperty("thumbnail") ? attachment.sizes.thumbnail.url : attachment.sizes.full.url,
					image_ID: attachment.id,
				  } : component
				  );
				  return { ...subg, components: newSubComponent };
			});
		return { ...group, components: newGroup, groups: subgorups };
		}

    

        return { ...group, components: newGroup };
      });
      setPizzaComponents(newPizza);
      //   console.log(attachment);
    });
    frame.open();
  };
  
  const addComponent = (groupId) => {
    const newPizza = pizzaComponents.map((group) => {
      if (group.id === groupId) {
        return { ...group, components: [...group.components, newComponent] };
      }
      return group;
    });
    setPizzaComponents(newPizza);
    setNewComponent({ ...newComponent, id: uuid() });
  };
  const deleteComponent = (id) => {
    const newPizza = pizzaComponents.map((group) => {
      const newComponents = group.components.filter((c) => c.id !== id);
      return { ...group, components: newComponents };
    });

    setPizzaComponents(newPizza);
  };
  const setGroupName = (e, groupId) => {
    const newPizza = pizzaComponents.map((group) => {
      return group.id === groupId ? { ...group, groupName: e.target.value } : group;
    });

    setPizzaComponents(newPizza);
  };
  let frameGroup;
  const uploadGroupImage = (id) => {
    if (frameGroup) {
      frameGroup.open();
      return;
    }
    frameGroup = wp.media({
      multiple: "false",
    });
    frameGroup.on("select", function () {
      let attachment = frameGroup.state().get("selection").first().toJSON();
      //   console.log(attachment);
      const newPizza = pizzaComponents.map((group) => {
        return group.id === id
          ? {
              ...group,
              groupImage: attachment.sizes.hasOwnProperty("thumbnail") ? attachment.sizes.thumbnail.url : attachment.sizes.full.url,
            }
          : group;
      });
      setPizzaComponents(newPizza);
      //   console.log(attachment);
    });
    frameGroup.open();
  };
  const setComponentWeight = (e, componentId) => {
    const newPizza = pizzaComponents.map((group) => {
      const newComponent = group.components.map((component) =>
        component.id === componentId ? { ...component, weight: e.target.value } : component,
      );
      return { ...group, components: newComponent };
    });

    setPizzaComponents(newPizza);
  };
  const setComponentDescription = (e, componentId) => {
    const newPizza = pizzaComponents.map((group) => {
      const newComponent = group.components.map((component) =>
        component.id === componentId ? { ...component, description: e.target.value } : component,
      );
      return { ...group, components: newComponent };
    });

    setPizzaComponents(newPizza);
  };
  const setComponentName = (e, componentId) => {
    const newPizza = pizzaComponents.map((group) => {
      const newComponent = group.components.map((component) =>
        component.id === componentId ? { ...component, name: e.target.value } : component,
      );
      return { ...group, components: newComponent };
    });

    setPizzaComponents(newPizza);
  };
  const setComponentPrice = (e, componentId) => {
    const newPizza = pizzaComponents.map((group) => {
      const newComponent = group.components.map((component) =>
        component.id === componentId ? { ...component, price: e.target.value } : component,
      );
      return { ...group, components: newComponent };
    });

    setPizzaComponents(newPizza);
  };
  const setComponentMeta = (e, componentId) => {
    const newPizza = pizzaComponents.map((group) => {
      const newComponent = group.components.map((component) =>
        component.id === componentId ? { ...component, meta: e.target.checked } : component,
      );
      return { ...group, components: newComponent };
    });

    setPizzaComponents(newPizza);
  };
  const addGroupPizza = () => {
    setPizzaComponents([
      ...pizzaComponents,
      {
        id: uuid(),
        groupName: "Secondary",
        groupImage: `${EV_PIZZA_DATA.url}images/placeholder.svg`,
        components: [],
      },
    ]);
  };
  const deleteGroup = (id) => {
    setPizzaComponents(pizzaComponents.filter((group) => group.id !== id));
  };
  /////////SETTINGS///////
  let settingFrame;
  const uploadImageSettings = (field) => {
    if (settingFrame) {
      settingFrame.open();
      return;
    }
    settingFrame = wp.media({
      multiple: "false",
    });
    settingFrame.on("select", function () {
      let attachment = settingFrame.state().get("selection").first().toJSON();
      //   console.log(attachment);

      setPizzaSettings({
        ...pizzaSettings,
        [field]: {
          ...pizzaSettings[field],
          image: attachment.sizes.hasOwnProperty("thumbnail") ? attachment.sizes.thumbnail.url : attachment.sizes.full.url,
          image_ID: attachment.id,
        },
      });
      //   console.log(attachment);
    });
    settingFrame.open();
  };
  const setPizzaTips = (e) => {
    setPizzaSettings({
      ...pizzaSettings,
      tipps: {
        enabled: e.target.checked,
      },
    });
  };

  const setPizzaBoolSetting = (e, field) => {
    setPizzaSettings({
      ...pizzaSettings,
      [field]: e.target.checked,
    });
  };
  const setMetaPopup = (e) => {
    setPizzaSettings({
      ...pizzaSettings,
      meta_popup: e.target.checked,
    });
  };
  const setShop = (e) => {
    setPizzaSettings({
      ...pizzaSettings,
      shop: e.target.checked,
    });
  };
  const setRedirectCart = (e) => {
    setPizzaSettings({
      ...pizzaSettings,
      redirect_cart: e.target.checked,
    });
  };
  const setShortcode = (e) => {
    setPizzaSettings({
      ...pizzaSettings,
      shortcode_disable: e.target.checked,
    });
  };

  const handleChangeProduct = (e) => {
    setPizzaSettings({
      ...pizzaSettings,
      shortcode_product: e.target.value,
    });
  };

  const handleChangeAttributes = (e) => {
	setPizzaSettings({
		...pizzaSettings,
		smooth_attributes: typeof e.target.value === 'string' ? e.target.value.split(',') : e.target.value,
	  });
  }

  const saveData = (section) => {
    setLoading(true);
    const formData = new FormData();
    const abortController = new AbortController();
    let textSaved = "Saved!";
    switch (section) {
      case "settings":
        formData.append("action", "ev_pizza_save_settings");
        formData.append("data", JSON.stringify(pizzaSettings));
        formData.append("nonce", EV_PIZZA_DATA.nonce);
        textSaved = "Settings was saved!";
        break;
      case "components":
        formData.append("action", "ev_pizza_save_components");
        formData.append("data", JSON.stringify(pizzaComponents));
        formData.append("nonce", EV_PIZZA_DATA.nonce);
        textSaved = "Components was saved!";
        break;
      case "shop":
        formData.append("action", "ev_pizza_save_shop");
        formData.append("data", JSON.stringify(pizzaSettings));
        formData.append("nonce", EV_PIZZA_DATA.nonce);
        textSaved = "Settings was saved!";
        break;
    }

    try {
      fetch(EV_PIZZA_DATA.ajax_url, {
        method: "POST",
        body: formData,
        signal: abortController.signal,
      })
        .then((response) => {
          return response.json();
        })
        .then((res) => {
          //console.log( res );
          if (res.success) {
            setMessage({
              opened: true,
              type: "success",
              text: textSaved,
            });
          } else {
            setMessage({
              opened: true,
              type: "error",
              text: __("Something went wrong", "pizza-builder-for-woocommerce"),
            });
          }
          setLoading(false);
        });
    } catch (err) {
      if (!abortController.signal.aborted) {
        setLoading(false);
        setMessage({
          opened: true,
          type: "error",
          text: __("Something went wrong", "pizza-builder-for-woocommerce"),
        });
      }
      console.log(err);
    }
  };

  const renderFooter = (section) => {
    let textSaved = "Save Settings";
    switch (section) {
      case "settings":
        textSaved = "Save Settings";
        break;
      case "components":
        textSaved = "Save Components";
        break;
      case "shop":
        textSaved = "Save Settings";
        break;
    }

    return (
      <Box sx={{ marginTop: "50px" }}>
        <LoadingButton
          color="success"
          onClick={() => saveData(section)}
          loading={loading}
          loadingPosition="start"
          startIcon={<SaveIcon />}
          variant="contained"
        >
          {textSaved}
        </LoadingButton>
        <Snackbar
          anchorOrigin={{ vertical: "top", horizontal: "right" }}
          open={message.opened}
          autoHideDuration={4000}
          onClose={() => setMessage({ ...message, opened: false })}
          key={"top-right"}
          sx={{ top: "70px !important" }}
        >
          <div>
            <Alert type={message.type}>{message.text}</Alert>
          </div>
        </Snackbar>
      </Box>
    );
  };

  return (
    <Paper>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs value={tab} onChange={handleChangeTab}>
          <Tab label={__("Settings")} {...tabProps(0)} />
          <Tab label={__("Components")} {...tabProps(1)} />
          <Tab
            label={
              <div>
                <Badge badgeContent="New" color="error">
                  <p>{__("Shop")}</p>
                </Badge>
              </div>
            }
            {...tabProps(2)}
          />
          <Tab label={__("Shortcode Builder")} {...tabProps(3)} />
          <Tab label={__("Developers")} {...tabProps(4)} />
        </Tabs>
      </Box>
      <TabPanel value={tab} index={0}>
        <Box sx={{ maxWidth: "150px", mb: 2 }}>
          <Typography component="h6" variant="h6">
            {__("General")}
          </Typography>
          <Divider />
        </Box>

        <FormControllSettings>
          <FormControlLabel
            control={<Checkbox checked={pizzaSettings.tipps.enabled} onChange={setPizzaTips} color="primary" />}
            label={__("Enable tips")}
          />
        </FormControllSettings>
        <FormControllSettings>
          <FormControlImage>
            <Typography sx={{ whiteSpace: "nowrap" }}>{__("Empty image for pizza layer")}</Typography>
            <ComponentMedia
              component="img"
              height="100"
              image={pizzaSettings.empty_layer.image}
              alt=""
              onClick={() => uploadImageSettings("empty_layer")}
            />
          </FormControlImage>
        </FormControllSettings>
        <FormControllSettings>
          <FormControlImage>
            <Typography sx={{ whiteSpace: "nowrap" }}>{__("Empty image for pizza side")}</Typography>
            <ComponentMedia
              component="img"
              height="100"
              image={pizzaSettings.empty_side.image}
              alt=""
              onClick={() => uploadImageSettings("empty_side")}
            />
          </FormControlImage>
        </FormControllSettings>
        <FormControllSettings>
          <FormControlLabel
            control={
              <Checkbox
                checked={pizzaSettings?.meta_popup ?? true}
                onChange={(e) => setPizzaBoolSetting(e, "meta_popup")}
                color="primary"
              />
            }
            label={__("Show cart/order meta in popup")}
          />
        </FormControllSettings>

        <Box sx={{ maxWidth: "200px", mt: 3, mb: 2 }}>
          <Typography component="h6" variant="h6">
            {__("Shortcode Builder")}
          </Typography>
          <Divider />
        </Box>

		
        <FormControllSettings>
			
			<FormControl sx={{minWidth:'400px'}}>
				<InputLabel >{__("PBW Product")}</InputLabel>
				<Select value={pizzaSettings?.shortcode_product ?? ""} label={__("PBW Product")} onChange={handleChangeProduct}>
					
					{EV_PIZZA_DATA.pbw_products.map((item) => (
					<MenuItem value={item.value}>{item.title}</MenuItem>
					))}
				</Select>
			</FormControl>
        </FormControllSettings>

        <FormControllSettings>
          <FormControlLabel
            control={
              <Checkbox
                checked={pizzaSettings?.redirect_cart ?? false}
                onChange={(e) => setPizzaBoolSetting(e, "redirect_cart")}
                color="primary"
              />
            }
            label={__("Redirect to Cart page")}
          />
        </FormControllSettings>
        <FormControllSettings>
          <FormControlLabel
            control={
              <Checkbox
                checked={pizzaSettings?.shortcode_disable ?? false}
                onChange={(e) => setPizzaBoolSetting(e, "shortcode_disable")}
                color="primary"
              />
            }
            label={__("Disable Shortcode Builder")}
          />
        </FormControllSettings>

		<Box sx={{ maxWidth: "300px", mt: 3, mb: 2 }}>
			<Badge badgeContent="New" color="error">
				<Typography component="h6" variant="h6">
					{__("Smooth Attribute Swatches")}
					
				</Typography>
		 	 </Badge>
			  <FormHelperText sx={{ ml: 0 }}>{__("(when using product Style #3 - Dodo)", "pizza-builder-for-woocommerce")}</FormHelperText>
          <Divider />
        </Box>

		<FormControllSettings>
			
			<FormControl sx={{minWidth:'400px'}}>
				<InputLabel >{__("Choose attributes")}</InputLabel>
				<Select value={pizzaSettings?.smooth_attributes ?? ""} multiple label={__("Choose attributes")} onChange={handleChangeAttributes}>
					
					{EV_PIZZA_DATA.smooth_attributes.map((item) => (
					<MenuItem value={item.value}>{item.label}</MenuItem>
					))}
				</Select>
			</FormControl>
        </FormControllSettings>

        {renderFooter("settings")}
      </TabPanel>
      <TabPanel value={tab} index={1}>
        {pizzaComponents.map((group) => (
          <Accordion
            expanded={expanded.hasOwnProperty(group.id) ? expanded[group.id] : false}
            onChange={handleChangeAcc(group.id)}
            key={`group-${group.id}`}
          >
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1a-content" id="panel1a-header">
              <Typography>{group.groupName}</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <AccordionHeaderInner>
                <FormControl>
                  <TextField
                    type="text"
                    label={__("Group name")}
                    name="group_name"
                    value={group.groupName}
                    onChange={(e) => setGroupName(e, group.id)}
                  />
                </FormControl>
                <FormControlImage>
                  <Box sx={{ width: "100%", marginRight: "10px" }}>
                    <Typography sx={{ whiteSpace: "nowrap" }}>{__("Group image")}</Typography>
                    <FormHelperText sx={{ ml: 0 }}>{__("(for tabs)", "pizza-builder-for-woocommerce")}</FormHelperText>
                  </Box>
                  <ComponentMedia component="img" height="100" image={group.groupImage} alt="" onClick={() => uploadGroupImage(group.id)} />
                </FormControlImage>
              </AccordionHeaderInner>
              <Typography component="h4" variant="h4" sx={{ padding: "10px 0" }}>
                {__("Components")}
              </Typography>
              <Grid container spacing={2}>
                {group.components.map((component) => (
                  <Grid item xs={6} md={3} sm={4} xl={2} key={`component-${component.id}`}>
                    <Card>
                      <CardHeader
                        avatar={
                          <Avatar aria-label="component">
                            {component.name.split("").length > 0 ? component.name.split("")[0].toUpperCase() : ""}
                          </Avatar>
                        }
                        action={
                          <div>
                            <IconButton aria-label="edit" color="secondary" onClick={() => handleExpandEdit(component.id)}>
                              <EditIcon />
                            </IconButton>
                            <IconButton aria-label="delete" color="error" onClick={() => deleteComponent(component.id)}>
                              <DeleteIcon />
                            </IconButton>
                          </div>
                        }
                        title={component.name}
                        subheader={
                          <span>
                            {component.weight !== "" && `${component.weight}/`}
                            {`${component.price} ${EV_PIZZA_DATA.wc_symbol}`}
                          </span>
                        }
						sx={{flexWrap: 'wrap'}}
                      ></CardHeader>
                      <Collapse
                        in={expandEdit.hasOwnProperty(component.id) ? expandEdit[component.id] : false}
                        timeout="auto"
                        unmountOnExit
                      >
                        <FormControlFull>
                          <TextField
                            type="text"
                            label={__("Name")}
                            name="component_name"
                            value={component.name}
                            onChange={(e) => setComponentName(e, component.id)}
                          />
                        </FormControlFull>
                        <FormControlFull>
                          <TextField
                            type="text"
                            label={__("Price")}
                            name="component_price"
                            value={component.price}
                            onChange={(e) => setComponentPrice(e, component.id)}
                          />
                        </FormControlFull>
                        <FormControlFull>
                          <CardMediaEdit src={component.image} height="50" onClick={() => uploadImage(component.id)} />
                        </FormControlFull>
                        <FormControlFull>
                          <TextField
                            label={__("Weight")}
                            name="component_weight"
                            value={component.weight}
                            onChange={(e) => setComponentWeight(e, component.id)}
                          />
                        </FormControlFull>
                        <FormControlFull>
                          <TextField
                            label={__("Description")}
                            multiline
                            maxRows={4}
                            value={component.description}
                            onChange={(e) => setComponentDescription(e, component.id)}
                          />
                        </FormControlFull>
                        <FormControlFull>
                          <FormControlLabel
                            control={
                              <Checkbox checked={component.meta} onChange={(e) => setComponentMeta(e, component.id)} color="warning" />
                            }
                            label={__("Also as meta")}
                          />
                        </FormControlFull>
                      </Collapse>
                      {(expandEdit.hasOwnProperty(component.id) ? !expandEdit[component.id] : true) && (
                        <>
                          <ComponentMedia
                            component="img"
                            height="100"
                            image={component.image}
                            alt=""
                            onClick={() => uploadImage(component.id)}
                          />
                          <CardContent>
                            <Typography variant="body2" color="text.secondary">
                              {component.description}
                            </Typography>
                          </CardContent>
                        </>
                      )}
                    </Card>
                  </Grid>
                ))}
              </Grid>
            </AccordionDetails>
            <AccordionActions>
              <Button variant="contained" color="secondary" onClick={() => addComponent(group.id)}>
                {__("Add component")}
              </Button>
              <Button variant="contained" color="error" onClick={() => deleteGroup(group.id)}>
                {__("Delete group")}
              </Button>
            </AccordionActions>
          </Accordion>
        ))}
        <Box sx={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
          <Tooltip title={__("Add group")} placement="top">
            <IconButtonAdd color="inherit" size="large" onClick={addGroupPizza}>
              <AddIcon />
            </IconButtonAdd>
          </Tooltip>
          {renderFooter("components")}
        </Box>
      </TabPanel>
      <TabPanel value={tab} index={2}>
        <FormControllSettings>
          <FormControlLabel
            control={
              <Checkbox
                checked={pizzaSettings?.quick_view ?? true}
                onChange={(e) => setPizzaBoolSetting(e, "quick_view")}
                color="primary"
              />
            }
            label={<div>{__("Quick View")}<FormHelperText sx={{ ml: 0 }}>{__("Allows to choose attributes and ingredient on archive pages", "pizza-builder-for-woocommerce")}</FormHelperText></div>}
          />
        </FormControllSettings>
        {renderFooter("shop")}
      </TabPanel>
      <TabPanel value={tab} index={3}>
        <PizzaShortCode data={pizzaComponents} />
      </TabPanel>
      <TabPanel value={tab} index={4}>
        <PizzaCode
          code={`
		/**
		 * Modify data for displaying components
		 * @return array $data
		 */ 
		add_filter('ev_pizza_components_data', function($data) {
		//
		return $data;
	    });

        /**
         * Modify html string for Add Layer title
         * @return string
         */
        add_filter('ev_pizza_add_layer_title', function($html) {
          //default is <span class="pizza-highlight">Add</span><span>layer</span>
          return $html;
        });

        /**
         * Change Add layer button name
         * @return string
         */ 
        add_filter('ev_pizza_add_layer_button', function($name) {
          //
          return $name;
        });

        /**
         * Modify html string for Add side title
         * @return string
         */ 
         add_filter('ev_pizza_add_side_title', function($html) {
          //default is <span class="pizza-highlight">Choose</span><span>side</span>
          return $html;
        });

        /**
         * Change Add side button name
         * @return string
         */ 
        add_filter('ev_pizza_add_side_button', function($name) {
          //
          return $name;
        });

        /**
         * Change 'Components extra:' text
         */
        add_filter('ev_pizza_components_adds_text', function($name, $product_id) {
          //
          return $name; 
        }, 10, 2); 

        /**
         * Change 'Basic Components:' text
         */
        add_filter('ev_pizza_components_basic_text', function($name, $product_id) {
          //
          return $name; 
        }, 10, 2); 

        /**
         * Change 'Layers:' text
         */
        add_filter('ev_pizza_components_layers_text', function($name, $product_id) {
          //
          return $name; 
        }, 10, 2); 

        /**
         * Change 'Side:' text
         */
        add_filter('ev_pizza_components_side_text', function($name, $product_id) {
          //
          return $name; 
        }, 10, 2); 

        /**
         * Change 'Pizza components:' text in description to product
         * @var WC_Order_Item_Meta $meta
         */
        add_filter('ev_pizza_checkout_meta_key', function($name, $meta) {
          //
          return $name; 
        }, 10, 2); 

        /**
         * Change 'Pizza composition' text in description to product
         */
        add_filter('ev_pizza_composition_text', function($name, $product_id) {
          //
          return $name; 
        },10 ,2); 
        
        `}
          lang="javascript"
        ></PizzaCode>
		<PizzaCode
		 code={`
		 
		 /**
		  * Screen size Swiper starts from.
		  * @since 2.5
		  */ 
		 add_action( 'wp_enqueue_scripts', 'custom_add_pizza_scripts' );
		 function custom_add_pizza_scripts() {
			wp_add_inline_script(
				'pizza-front',
				"window.evPizzaHooks.pizzaHooks.addFilter('ev-pizza-swiper-media-starts','pizza-builder-for-woocommerce', function(screen, instance) {
				//
				console.log(screen, instance);
				//screen = 990;
				return screen;
				});",
				'before'
			);
		 }

		 /**
		  * Swiper args for components slider.
		  * @since 2.5
		  */ 
		 add_action( 'wp_enqueue_scripts', 'custom_add_pizza_scripts' );
		 function custom_add_pizza_scripts() {
			wp_add_inline_script(
				'pizza-front',
				"window.evPizzaHooks.pizzaHooks.addFilter('ev-pizza-components-swiper-args','pizza-builder-for-woocommerce', function(args, instance) {
				//
				console.log(args, instance);
				//args['slidesPerView'] = 2.4;
				return args;
				});",
				'before'
			);
		 }

		 /**
		  * On init.
		  * @since 2.5
		  */ 
		 add_action( 'wp_enqueue_scripts', 'custom_add_pizza_scripts' );
		 function custom_add_pizza_scripts() {
			wp_add_inline_script(
				'pizza-front',
				"window.evPizzaHooks.pizzaHooks.addAction('ev-pizza-init','pizza-builder-for-woocommerce', function(instance) {
					console.log(instance);
				
				});",
				'before'
			);
		 }

		 /**
		  * On Buy Product via Shortcode Builder.
		  * Use this hook to replace 'Done' Success message.
		  * @since 2.5
		  */
		 add_action( 'wp_enqueue_scripts', 'custom_add_pizza_scripts' );
		 function custom_add_pizza_scripts() {
			wp_add_inline_script(
				'pizza-builder',
				"window.evPizzaHooks.builderHooks.addFilter('ev-pizza-after-builder-request','pizza-builder-for-woocommerce', function(func, res, instance) {
					console.log(res, instance);
					return () => {
						//your actions
					};
				});",
				'before'
			);	
		 }
		 `}
		 lang="javascript"
		>

		</PizzaCode>
      </TabPanel>
    </Paper>
  );
};

createRoot(document.getElementById("admin-pizza-app")).render(<AdminPizza />);
